/* =========================================
   TAMBAHAN KHUSUS REGISTER FORM
   ========================================= */

/* Animasi Masuk (Slide Up Fade) */
.register-form-anim {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    /* Animasi halus saat form muncul */
    animation: formSlideUp 0.5s cubic-bezier(0.23, 1, 0.32, 1) forwards;
  }
  
  @keyframes formSlideUp {
    from {
      opacity: 0;
      transform: translateY(15px); /* Mulai dari sedikit di bawah */
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  /* Penyesuaian Layout Khusus Register 
     (Agar muat 3 input tanpa terlihat sempit) */
  
  .register-form-anim h1 {
    font-size: 1.8rem; /* Sedikit lebih kecil dari Login agar proporsional */
    margin-bottom: 0.2rem;
  }
  
  .register-form-anim .subtitle {
    margin-bottom: 1.5rem; /* Jarak ke input pertama lebih dekat */
    font-size: 0.9rem;
  }
  
  .register-form-anim .input-group {
    margin-bottom: 1rem; /* Jarak antar input lebih rapat dibanding login */
  }
  
  /* Responsif Mobile untuk Register */
  @media (max-width: 768px) {
    .register-form-anim {
      padding-top: 1rem;
    }
    
    .register-form-anim h1 {
      margin-top: 0;
    }
  }