/* =========================================
   AUTH PAGE - ULTRA PREMIUM RSIA STYLE
   ========================================= */

   :root {
    /* Warna Brand Utama */
    --primary-color: #3eb7b3; /* Toska Annisa */
    --primary-gradient-start: #3eb7b3;
    --primary-gradient-end: #2fa29e;
    
    /* Background & Netral */
    --bg-page: #f8fafc; /* Abu-abu sangat muda */
    --white: #ffffff;
    --text-main: #1e293b;
    --text-muted: #64748b;
    --border-color: #e2e8f0;
    
    /* Effect */
    --shadow-card: 
      0 20px 25px -5px rgba(0, 0, 0, 0.1), 
      0 8px 10px -6px rgba(0, 0, 0, 0.1);
    --shadow-input-focus: 0 0 0 4px rgba(62, 183, 179, 0.15);
    --radius-card: 28px;
    --radius-input: 16px;
    --transition-smooth: 0.6s cubic-bezier(0.23, 1, 0.32, 1);
  }
  
  /* --- LAYOUT UTAMA --- */
  .authpage {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100vh;
    background-color: var(--bg-page);
    font-family: 'Poppins', sans-serif;
    overflow: hidden;
    position: relative;
  }
  
  /* Background Shapes Animasi */
  .bg-shape {
    position: absolute;
    border-radius: 50%;
    filter: blur(80px);
    z-index: 0;
    opacity: 0.6;
  }
  
  .shape-1 {
    width: 400px;
    height: 400px;
    background: rgba(62, 183, 179, 0.25);
    top: -100px;
    left: -100px;
    animation: float 10s infinite ease-in-out;
  }
  
  .shape-2 {
    width: 300px;
    height: 300px;
    background: rgba(45, 212, 191, 0.2);
    bottom: -50px;
    right: -50px;
    animation: float 12s infinite ease-in-out reverse;
  }
  
  @keyframes float {
    0%, 100% { transform: translate(0, 0); }
    50% { transform: translate(20px, 40px); }
  }
  
  /* --- MAIN CONTAINER (CARD) --- */
  .container {
    background-color: var(--white);
    border-radius: var(--radius-card);
    box-shadow: var(--shadow-card);
    position: relative;
    overflow: hidden;
    width: 950px;
    max-width: 92%;
    min-height: 550px;
    z-index: 1;
    display: flex;
    margin-top: 10px;
  }
  
  /* --- FORM AREA --- */
  .form-container {
    position: absolute;
    top: 0;
    height: 100%;
    transition: all var(--transition-smooth);
    padding: 0 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 50%;
    background: var(--white);
  }
  
  form {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  h1 {
    font-weight: 800;
    margin-bottom: 0.5rem;
    color: var(--text-main);
    font-size: 2rem;
    letter-spacing: -0.5px;
  }
  
  .subtitle {
    font-size: 0.95rem;
    color: var(--text-muted);
    margin-bottom: 2.5rem;
    font-weight: 400;
    line-height: 1.5;
  }
  
  /* --- MODERN INPUT STYLE --- */
  .input-group {
    position: relative;
    width: 100%;
    margin-bottom: 1.25rem;
  }
  
  .input-icon {
    position: absolute;
    top: 50%;
    left: 18px;
    transform: translateY(-50%);
    color: #94a3b8;
    transition: color 0.3s ease;
    pointer-events: none;
  }
  
  .auth-input {
    background-color: #f8fafc;
    border: 1px solid var(--border-color);
    padding: 16px 16px 16px 52px; /* Ruang untuk ikon */
    width: 100%;
    border-radius: var(--radius-input);
    font-size: 0.95rem;
    color: var(--text-main);
    transition: all 0.3s ease;
    outline: none;
    font-weight: 500;
  }
  
  .auth-input::placeholder {
    color: #cbd5e1;
    font-weight: 400;
  }
  
  .auth-input:focus {
    background-color: var(--white);
    border-color: var(--primary-color);
    box-shadow: var(--shadow-input-focus);
  }
  
  .auth-input:focus + .input-icon {
    color: var(--primary-color);
  }
  
  .toggle-password {
    position: absolute;
    top: 50%;
    right: 18px;
    transform: translateY(-50%);
    color: #94a3b8;
    cursor: pointer;
    display: flex;
    transition: color 0.2s;
  }
  
  .toggle-password:hover {
    color: var(--text-main);
  }
  
  /* --- ACTIONS (Checkboxes, etc) --- */
  .form-actions {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1.5rem;
  }
  
  .remember {
    display: flex;
    align-items: center;
    font-size: 0.9rem;
    color: var(--text-muted);
    cursor: pointer;
  }
  
  .remember input {
    margin-right: 10px;
    accent-color: var(--primary-color);
    width: 18px;
    height: 18px;
    cursor: pointer;
    border-radius: 4px;
  }
  
  .remember label {
    cursor: pointer;
    user-select: none;
  }
  
  /* --- BUTTONS (Gradient Style) --- */
  .btn-primary {
    width: 100%;
    padding: 16px;
    border-radius: var(--radius-input);
    border: none;
    background: linear-gradient(135deg, var(--primary-gradient-start) 0%, var(--primary-gradient-end) 100%);
    color: white;
    font-size: 1rem;
    font-weight: 700;
    cursor: pointer;
    transition: all 0.3s ease;
    box-shadow: 0 10px 20px -10px rgba(62, 183, 179, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    letter-spacing: 0.5px;
  }
  
  .btn-primary:hover {
    transform: translateY(-2px);
    box-shadow: 0 15px 25px -10px rgba(62, 183, 179, 0.6);
  }
  
  .btn-primary:active {
    transform: scale(0.98);
  }
  
  .btn-primary:disabled {
    background: #cbd5e1;
    cursor: not-allowed;
    box-shadow: none;
    transform: none;
  }
  
  /* Divider & Links */
  .divider {
    display: flex;
    align-items: center;
    width: 100%;
    margin: 1.5rem 0;
    color: #cbd5e1;
  }
  
  .divider::before, .divider::after {
    content: "";
    flex: 1;
    border-bottom: 1px solid #e2e8f0;
  }
  
  .divider span {
    padding: 0 10px;
    font-size: 0.85rem;
    font-weight: 500;
    color: #94a3b8;
  }
  
  .switch-text {
    font-size: 0.9rem;
    color: var(--text-muted);
  }
  
  .text-link {
    color: var(--primary-color);
    font-weight: 700;
    cursor: pointer;
    text-decoration: none;
    margin-left: 5px;
    transition: color 0.2s;
  }
  
  .text-link:hover {
    color: var(--primary-gradient-end);
    text-decoration: underline;
  }
  
  /* --- SLIDING LOGIC (DO NOT TOUCH) --- */
  .sign-in-container {
    left: 0;
    width: 50%;
    z-index: 2;
  }
  
  .sign-up-container {
    left: 0;
    width: 50%;
    opacity: 0;
    z-index: 1;
  }
  
  .container.right-panel-active .sign-in-container {
    transform: translateX(100%);
    opacity: 0;
  }
  
  .container.right-panel-active .sign-up-container {
    transform: translateX(100%);
    opacity: 1;
    z-index: 5;
    animation: fadeIn 0.6s;
  }
  
  @keyframes fadeIn {
    0%, 49.99% { opacity: 0; z-index: 1; }
    50%, 100% { opacity: 1; z-index: 5; }
  }
  
  /* --- OVERLAY CONTAINER --- */
  .overlay-container {
    position: absolute;
    top: 0;
    left: 50%;
    width: 50%;
    height: 100%;
    overflow: hidden;
    transition: transform var(--transition-smooth);
    z-index: 100;
    border-radius: 0 var(--radius-card) var(--radius-card) 0;
  }
  
  .container.right-panel-active .overlay-container {
    transform: translateX(-100%);
    border-radius: var(--radius-card) 0 0 var(--radius-card);
  }
  
  /* ======================================================== */
  /* ðŸŽ¨ OVERLAY: BACKGROUND IMAGE + GRADIENT BLEND ðŸŽ¨ */
  /* ======================================================== */
  .overlay {
    /* 1. Gambar Header Anisa */
    background-image: url('../assets/header-anisa.jpg');
    background-size: cover;
    background-position: center;
    
    /* 2. Pseudo-element untuk gradient overlay yang elegan */
    position: relative;
    left: -100%;
    height: 100%;
    width: 200%;
    transform: translateX(0);
    transition: transform var(--transition-smooth);
  }
  
  /* Layer warna di atas gambar */
  .overlay::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    /* Gradient Toska Gelap agar teks putih terbaca jelas */
    background: linear-gradient(135deg, rgba(62, 183, 179, 0.85) 0%, rgba(30, 41, 59, 0.85) 100%);
    mix-blend-mode: multiply; /* Kunci efek premium */
    z-index: 1;
  }
  
  .container.right-panel-active .overlay {
    transform: translateX(50%);
  }
  
  .overlay-panel {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 0 40px;
    text-align: center;
    top: 0;
    height: 100%;
    width: 50%;
    transform: translateX(0);
    transition: transform var(--transition-smooth);
    z-index: 2; /* Di atas layer warna */
  }
  
  .overlay-panel h1 {
    color: white;
    margin-bottom: 12px;
    text-shadow: 0 2px 10px rgba(0,0,0,0.3);
  }
  
  .overlay-panel p {
    color: rgba(255, 255, 255, 0.9);
    font-size: 1rem;
    line-height: 1.6;
    font-weight: 300;
    margin-bottom: 35px;
    max-width: 80%;
  }
  
  /* Ghost Button (Outline) */
  .ghost {
    background-color: transparent;
    border: 2px solid rgba(255,255,255,0.7);
    color: #ffffff; 
    cursor: pointer;
    padding: 12px 40px;
    border-radius: 50px;
    font-weight: 700;
    font-size: 0.9rem;
    transition: all 0.3s ease;
    letter-spacing: 0.5px;
    backdrop-filter: blur(4px);
  }
  
  .ghost:hover {
    background-color: #ffffff;
    color: var(--primary-color);
    border-color: #ffffff;
    transform: translateY(-2px);
    box-shadow: 0 5px 15px rgba(0,0,0,0.2);
  }
  
  /* Posisi Panel */
  .overlay-left { transform: translateX(-20%); }
  .container.right-panel-active .overlay-left { transform: translateX(0); }
  .overlay-right { right: 0; transform: translateX(0); }
  .container.right-panel-active .overlay-right { transform: translateX(20%); }
  
  /* --- MOBILE RESPONSIVE --- */
  @media (max-width: 768px) {
    .container {
      width: 100%;
      min-height: 100vh;
      border-radius: 0;
      box-shadow: none;
    }
  
    .overlay-container { display: none; } /* Hide overlay on mobile */
  
    .form-container {
      width: 100%;
      padding: 2rem;
      opacity: 1 !important;
      transform: none !important;
      justify-content: center;
    }
  
    .sign-in-container, .sign-up-container {
      display: flex; 
    }
  
    /* Logic Toggle Mobile */
    .container.mobile-admin-mode .sign-in-container { display: none; }
    .container.mobile-admin-mode .sign-up-container {
      display: flex;
      animation: fadeIn 0.5s ease-out;
    }
  
    .mobile-switch-btn {
      display: flex !important;
      align-items: center;
      justify-content: center;
      background: #f1f5f9;
      border: none;
      color: var(--text-main);
      font-weight: 600;
      margin-top: 2rem;
      padding: 14px;
      border-radius: 12px;
      width: 100%;
      cursor: pointer;
      transition: background 0.2s;
      gap: 8px;
    }
    
    .mobile-switch-btn:active { background: #e2e8f0; }
  }
  
  .mobile-switch-btn { display: none; }
  
  /* Loader CSS */
  .loader {
    width: 20px;
    height: 20px;
    border: 2px solid #FFF;
    border-bottom-color: transparent;
    border-radius: 50%;
    display: inline-block;
    animation: rotation 1s linear infinite;
  }
  @keyframes rotation { 0% { transform: rotate(0deg); } 100% { transform: rotate(360deg); } }