/* =========================================
   SCREEN LOADING - MEDICAL PREMIUM STYLE
   ========================================= */

   :root {
    --primary-tosca: #3eb7b3;
    --primary-dark: #2b918d;
    --bg-light: #f0fdfa;
    --text-main: #1e293b;
    --text-muted: #64748b;
  }
  
  /* --- MAIN CONTAINER --- */
  .hospital-loader-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: var(--bg-light);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    z-index: 9999;
    overflow: hidden;
    font-family: 'Poppins', sans-serif;
  }
  
  /* Background Abstract Glow */
  .bg-gradient-orb {
    position: absolute;
    width: 600px;
    height: 600px;
    background: radial-gradient(circle, rgba(62, 183, 179, 0.15) 0%, transparent 70%);
    border-radius: 50%;
    animation: breathe 4s infinite ease-in-out;
    z-index: 0;
  }
  
  .loader-content {
    position: relative;
    z-index: 10;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 40px;
    background: rgba(255, 255, 255, 0.6);
    backdrop-filter: blur(10px); /* Glass effect */
    border-radius: 30px;
    box-shadow: 0 20px 50px rgba(62, 183, 179, 0.15);
    border: 1px solid rgba(255, 255, 255, 0.8);
    width: 90%;
    max-width: 400px;
  }
  
  /* --- LOGO ANIMATION --- */
  .logo-wrapper {
    position: relative;
    width: 100px;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
  }
  
  .hospital-logo {
    width: 80px;
    height: auto;
    position: relative;
    z-index: 2;
    filter: drop-shadow(0 4px 6px rgba(0,0,0,0.1));
    animation: logoFloat 3s ease-in-out infinite;
  }
  
  /* Pulse Rings (Efek Radar/Detak Jantung) */
  .pulse-ring {
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    border: 2px solid var(--primary-tosca);
    opacity: 0;
    top: 0;
    left: 0;
    animation: pulseRipple 2s linear infinite;
  }
  
  .pulse-ring.delay {
    animation-delay: 1s;
  }
  
  /* --- TEXT STYLING --- */
  .loading-text h2 {
    font-size: 1.4rem;
    font-weight: 700;
    color: var(--text-main);
    margin: 0 0 5px 0;
    letter-spacing: 0.5px;
  }
  
  .loading-text p {
    font-size: 0.9rem;
    color: var(--text-muted);
    margin: 0 0 25px 0;
    font-weight: 400;
  }
  
  /* --- HEARTBEAT SVG LINE --- */
  .heartbeat-wrapper {
    width: 120px;
    height: 40px;
    margin-bottom: 20px;
    overflow: hidden;
  }
  
  .heartbeat-svg {
    width: 100%;
    height: 100%;
  }
  
  .heartbeat-path {
    stroke-dasharray: 500;
    stroke-dashoffset: 500;
    animation: drawHeartbeat 2s linear infinite;
  }
  
  /* --- PROGRESS BAR --- */
  .progress-container {
    width: 100%;
    height: 6px;
    background-color: #e2e8f0;
    border-radius: 10px;
    overflow: hidden;
    position: relative;
  }
  
  .progress-bar {
    height: 100%;
    background: linear-gradient(90deg, var(--primary-tosca), var(--primary-dark));
    width: 0%;
    border-radius: 10px;
    animation: progressFill 2.5s ease-in-out forwards; /* 2.5s simulasi loading */
  }
  
  /* ================= KEYFRAMES ================= */
  
  /* Animasi Napas Background */
  @keyframes breathe {
    0%, 100% { transform: scale(1); opacity: 0.8; }
    50% { transform: scale(1.2); opacity: 1; }
  }
  
  /* Animasi Logo Mengambang */
  @keyframes logoFloat {
    0%, 100% { transform: translateY(0); }
    50% { transform: translateY(-5px); }
  }
  
  /* Animasi Lingkaran Keluar (Radar) */
  @keyframes pulseRipple {
    0% { transform: scale(0.8); opacity: 0.8; }
    100% { transform: scale(1.8); opacity: 0; }
  }
  
  /* Animasi Garis Detak Jantung */
  @keyframes drawHeartbeat {
    0% { stroke-dashoffset: 500; opacity: 0; }
    10% { opacity: 1; }
    80% { stroke-dashoffset: 0; opacity: 1; }
    100% { stroke-dashoffset: -500; opacity: 0; }
  }
  
  /* Animasi Loading Bar */
  @keyframes progressFill {
    0% { width: 0%; }
    30% { width: 40%; }
    60% { width: 70%; }
    100% { width: 100%; }
  }