/* =========================================================
   MODAL STYLES - ULTRA PREMIUM GLASS
   ========================================================= */

/* --- VARIABLES --- */
:root {
  --modal-z-index: 9999;
  --modal-bg-overlay: rgba(15, 23, 42, 0.65); /* Darker navy overlay for better focus */
  --modal-bg-card: rgba(255, 255, 255, 0.95); /* Hampir solid, sedikit transparan */
  
  --modal-radius: 24px; /* Sudut lebih bulat modern */
  --modal-shadow-depth: 
    0 0 0 1px rgba(255, 255, 255, 0.1) inset, /* Inner border light */
    0 25px 50px -12px rgba(0, 0, 0, 0.25), /* Deep shadow */
    0 0 15px rgba(0,0,0,0.05); /* Glow */

  /* Typography */
  --font-title: 'Inter', system-ui, sans-serif;
  --color-text-main: #111827;
  --color-text-muted: #64748b;

  /* State Colors (Modern Palette) */
  --success-bg: #ecfdf5; --success-text: #059669; --success-border: #a7f3d0;
  --error-bg: #fef2f2;   --error-text: #dc2626;   --error-border: #fecaca;
  --warning-bg: #fffbeb; --warning-text: #d97706; --warning-border: #fde68a;
  --info-bg: #eff6ff;    --info-text: #2563eb;    --info-border: #bfdbfe;
}

/* --- BACKDROP (Smooth Fade) --- */
.modal-backdrop {
  position: fixed;
  inset: 0; /* Shorthand for top, right, bottom, left */
  background-color: var(--modal-bg-overlay);
  backdrop-filter: blur(8px); /* Blur latar belakang */
  -webkit-backdrop-filter: blur(8px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: var(--modal-z-index);
  opacity: 0;
  visibility: hidden;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  padding: 1.5rem;
}

.modal-backdrop.show {
  opacity: 1;
  visibility: visible;
}

/* --- MODAL CARD (The Star Show) --- */
.modal-card {
  width: 100%;
  max-width: 440px;
  background: var(--modal-bg-card);
  
  /* Efek Glass pada Card itu sendiri (opsional, tergantung selera) */
  backdrop-filter: blur(12px); 
  -webkit-backdrop-filter: blur(12px);

  border-radius: var(--modal-radius);
  box-shadow: var(--modal-shadow-depth);
  padding: 32px 28px;
  text-align: center;
  
  /* Animasi Masuk */
  transform: scale(0.9) translateY(20px);
  opacity: 0;
  transition: all 0.4s cubic-bezier(0.34, 1.56, 0.64, 1); /* Efek memantul (bouncy) */
  position: relative;
  overflow: hidden;
}

.modal-backdrop.show .modal-card {
  transform: scale(1) translateY(0);
  opacity: 1;
}

/* --- HEADER & ICON WRAPPER --- */
.modal-header-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 16px;
  margin-bottom: 12px;
}

/* Icon Container - Lebih Besar & Glowing */
.icon-wrapper {
  width: 72px;
  height: 72px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 8px;
  font-size: 32px;
  
  /* Default state */
  background: #f3f4f6;
  color: #6b7280;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.05);
}

/* Animasi Icon Pop */
.modal-backdrop.show .icon-wrapper {
  animation: popIn 0.6s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards;
}

@keyframes popIn {
  0% { transform: scale(0); opacity: 0; }
  100% { transform: scale(1); opacity: 1; }
}

/* Icon Color Variants (Gradient & Soft Shadow) */
.icon-wrapper.bg-success {
  background: linear-gradient(135deg, #d1fae5 0%, #ecfdf5 100%);
  color: var(--success-text);
  box-shadow: 0 8px 16px -4px rgba(16, 185, 129, 0.2);
  border: 1px solid var(--success-border);
}

.icon-wrapper.bg-error {
  background: linear-gradient(135deg, #fee2e2 0%, #fef2f2 100%);
  color: var(--error-text);
  box-shadow: 0 8px 16px -4px rgba(220, 38, 38, 0.2);
  border: 1px solid var(--error-border);
}

.icon-wrapper.bg-warning {
  background: linear-gradient(135deg, #fef3c7 0%, #fffbeb 100%);
  color: var(--warning-text);
  box-shadow: 0 8px 16px -4px rgba(217, 119, 6, 0.2);
  border: 1px solid var(--warning-border);
}

.icon-wrapper.bg-info {
  background: linear-gradient(135deg, #dbeafe 0%, #eff6ff 100%);
  color: var(--info-text);
  box-shadow: 0 8px 16px -4px rgba(37, 99, 235, 0.2);
  border: 1px solid var(--info-border);
}

.icon-wrapper.bg-loading {
  background: #eef2ff;
  color: #4f46e5;
}

/* SVG Icon Size */
.modal-icon {
  width: 36px;
  height: 36px;
}

/* --- TYPOGRAPHY --- */
.modal-title {
  font-family: var(--font-title);
  font-size: 1.5rem;
  font-weight: 800;
  color: var(--color-text-main);
  margin: 0;
  letter-spacing: -0.025em; /* Tampilan modern rapat */
}

.modal-body p {
  font-family: var(--font-title);
  color: var(--color-text-muted);
  font-size: 1rem;
  line-height: 1.6;
  margin: 0 0 32px 0;
}

/* --- CLOSE BUTTON (Floating) --- */
.modal-close-icon {
  position: absolute;
  top: 20px;
  right: 20px;
  background: transparent;
  border: none;
  font-size: 1.5rem;
  color: #94a3b8;
  cursor: pointer;
  width: 36px;
  height: 36px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
}

.modal-close-icon:hover {
  background-color: #f1f5f9;
  color: #ef4444;
  transform: rotate(90deg);
}

/* --- BUTTONS & FOOTER --- */
.modal-footer {
  display: flex;
  gap: 16px;
  justify-content: center;
  width: 100%;
}

.btn {
  padding: 12px 24px;
  border-radius: 12px;
  font-size: 0.95rem;
  font-weight: 600;
  cursor: pointer;
  border: none;
  transition: all 0.2s cubic-bezier(0.4, 0, 0.2, 1);
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: var(--font-title);
}

/* Button Secondary (Batal) */
.btn-secondary {
  background-color: #ffffff;
  color: #475569;
  border: 1px solid #cbd5e1;
  box-shadow: 0 1px 2px rgba(0,0,0,0.05);
}

.btn-secondary:hover {
  background-color: #f8fafc;
  border-color: #94a3b8;
  color: #1e293b;
  transform: translateY(-1px);
}

/* Button Primary (Aksi) */
.btn-primary {
  color: white;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
}

.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  filter: brightness(105%);
}

.btn-primary:active {
  transform: translateY(0);
}

/* Dynamic Button Colors */
.btn-success { background: #10b981; }
.btn-error   { background: #ef4444; }
.btn-warning { background: #f59e0b; }
.btn-info, .btn-primary { background: #2563eb; }

/* Loading Spinner Animation */
.loading-spinner {
  animation: spin 1.2s linear infinite;
}

@keyframes spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

/* --- MOBILE RESPONSIVE --- */
@media (max-width: 640px) {
  .modal-card {
    padding: 24px 20px;
    width: 95%;
    max-width: 95%;
    border-radius: 20px;
  }
  
  .icon-wrapper {
    width: 60px;
    height: 60px;
  }
  
  .modal-title {
    font-size: 1.3rem;
  }
  
  .modal-body p {
    font-size: 0.9rem;
    margin-bottom: 24px;
  }

  .modal-footer {
    flex-direction: column-reverse; /* Tombol aksi di atas tombol batal */
    gap: 12px;
  }
  
  .btn {
    width: 100%;
    padding: 14px; /* Area tap lebih besar untuk jempol */
    font-size: 1rem;
  }
}